function setHeight(el){
    let clientHeight = document.documentElement.clientHeight;
    el.style.minHeight = clientHeight + 'px';
};  //функция принимает элемент и устанавливает его высоту, равную высоте экрана устройства


function addClassEach(arr, cl){    
    for(let i=0; i<arr.length; i++){
      arr[i].classList.add(cl);
    };
}; //функция принимает массив элементов и имя класса. IE11 не поддерживает forEach, поэтому так


function isView(el){
    let clientHeight = document.documentElement.clientHeight;
    let dec = el.getBoundingClientRect().top + el.getBoundingClientRect().height;
    if(dec<clientHeight && dec>0){
      return true;
    };
    return false;
};//функция венет true, если элемент в видимой области по вертикали

;(function(){

  const aboutBox = document.querySelector('.about-box');
  setHeight(aboutBox);
  
  })();

/*contacts*/ 

;(function(){

  const contactBox = document.querySelector('.contact-box');
  setHeight(contactBox);

  const buttonUp = document.querySelector('.button-page-up');

  buttonUp.addEventListener('click', ()=>{
      
      let x = setInterval(()=>{window.scrollBy(0, -100);}, 20);
      setTimeout(function(){clearInterval(x)}, 1500);
  });
  
})();

/*face*/

;(function(){
    
  const faceBox = document.querySelector('.face-box');
  setHeight(faceBox);
  
  })();

  /*forall*/

  ;(function(){

    let buttonNextPage = document.querySelectorAll('.button-next-page');
    
    for(let i=0; i<buttonNextPage.length; i++){
    
    buttonNextPage[i].addEventListener('click', function(){
        let nextPage = this.parentElement.nextElementSibling;
        let x = setInterval(goNextPage, 1);
        
        function goNextPage(){
            let y = nextPage.getBoundingClientRect().top;
            if(y>0){
                scrollBy(0, 20);
            }else{
                clearInterval(x);
            };
        };
    });
    };
    
    //для всплывания букв каждого заголовка
    const headerBlocks = document.querySelectorAll('.header-block');
    
    for(let i = 0; i<headerBlocks.length; i++){
        let lettersBack = headerBlocks[i].querySelectorAll('.text-back span');
        document.addEventListener('scroll', function(){showLetter(lettersBack);});
    };
    
    function showLetter(lettersBack){
        if(isView(lettersBack[0])){
            addClassEach(lettersBack, 'show-text-back');
            document.removeEventListener('scroll', showLetter);
        };
    };
    
    })();
    
/*load*/

window.addEventListener('load', ()=>{

  let loadBox = document.querySelector('.loader');
  let mainBox = document.querySelector('.main-box');
  let navButton = document.querySelector('.nav-button');
  let buttonNextPage = document.querySelector('.button-next-page');


  //временный код для теста, потом нужно удалить
  setTimeout(()=>{mainBox.style.display="block"; loadBox.style.display="none"; hell();}, 1000);
  setTimeout(()=>{navButton.style.opacity="1";}, 2000);
  setTimeout(()=>{buttonNextPage.style.opacity="0.5";}, 3000);

  
  function hell(){

      const line = document.querySelector('.line-anime');
      line.classList.add('line-goanime');
      const oneText = document.querySelector('.one-text-face');
      const twoText = document.querySelector('.two-text-face');                    
      oneText.classList.add('one-goanime');
      twoText.classList.add('two-goanime');
  }

});

/*navigator*/

;(function(){

  const nav = document.querySelector('.nav-button');
  const lines = document.querySelectorAll('.nav-button div');
  const navMain = document.querySelector('.nav-main');
  const links = navMain.querySelectorAll("a");
  const oneLine = lines[0];
  const twoLine = lines[1];
  const threeLine = lines[2];

  nav.addEventListener('click', ()=>{
      oneLine.classList.toggle('o');
      twoLine.classList.toggle('t');
      threeLine.classList.toggle('o');
      navMain.classList.toggle('shsh');
  });

  for(let i = 0; i<links.length; i++){

      links[i].addEventListener('click', ()=>{
          navMain.classList.remove('shsh');
          oneLine.classList.remove('o');
          twoLine.classList.remove('t');
          threeLine.classList.remove('o');
      });
  };
  
})();


/*skils*/

;(function(){
    
  let headText = document.querySelector('.mainskils h3');
  let divs = document.querySelectorAll('.mainskils-flex div');
  
  function showHeaderText(){
      if(isView(headText)){
          headText.classList.add('showskils-h3');
          document.removeEventListener('scroll', showHeaderText);
      };
  };
  
  function showDiv(){
      if(isView(divs[0])){
          addClassEach(divs, 'showskils-div');
          document.removeEventListener('scroll', showDiv);
      };
  };
  
  document.addEventListener('scroll', showHeaderText);
  document.addEventListener('scroll', showDiv);
  
  
  const skilsBox = document.querySelector('.skils-box');
  setHeight(skilsBox);
  
  
  //появление надписи при наведении, лучше изменить эффект
  const moreSkilsText = document.querySelectorAll('.moreskils-flex div');
  
  for(let i = 0; i<moreSkilsText.length; i++){
      moreSkilsText[i].addEventListener('mouseover', function(){
          this.querySelector('p').classList.add('moreskils-p-show');
      });
  
      moreSkilsText[i].addEventListener('mouseout', function(){
          this.querySelector('p').classList.remove('moreskils-p-show');
      });
  };
  
  })();

/*slider*/

;(function(){
  //устанавливаем минимальную высоту страницы высоте экрана
  const sliderBox = document.querySelector('.slider-box');
  setHeight(sliderBox);


  //вешаем обработчики на кнопки лево/право и создаем функции
  const leftBottom = document.querySelector('.slider-list .bottom-left');
  const rightBottom = document.querySelector('.slider-list .bottom-right');
  const ul = document.querySelector('.slider-list ul');
  let timeInterval = 0;

  leftBottom.addEventListener('click', left);
  rightBottom.addEventListener('click', right);

  function goInterval(){
      if(timeInterval==0){
      timeInterval = setInterval(right, 5000);
      };
  };              //функция запуска слайдера в интервале. Запускается автоматически при загрузки страницы

  function right(){

      clearInterval(timeInterval);
      timeInterval=0;
      
      const firstEl = ul.firstElementChild;
      const rightxx = ul.lastElementChild;
      const rightx = rightxx.previousElementSibling;
      const view = rightx.previousElementSibling;
      const leftx = view.previousElementSibling;
      const leftxx = leftx.previousElementSibling; 

      rightxx.classList.replace('rightxx', 'rightx');
      rightx.classList.replace('rightx', 'view');
      view.classList.replace('view', 'leftx');
      leftx.classList.replace('leftx', 'leftxx');
      leftxx.classList.remove('leftxx');
      firstEl.remove(firstEl);
      firstEl.classList.add('rightxx');
      ul.append(firstEl);

      goInterval();
  };

  function left(){

      clearInterval(timeInterval);
      timeInterval=0;

      const rightxx = ul.lastElementChild;
      const rightx = rightxx.previousElementSibling;
      const view = rightx.previousElementSibling;
      const leftx = view.previousElementSibling;
      const leftxx = leftx.previousElementSibling;

      leftxx.classList.replace('leftxx', 'leftx');
      leftx.classList.replace('leftx', 'view');
      view.classList.replace('view', 'rightx');
      rightx.classList.replace('rightx', 'rightxx');
      rightxx.remove();
      rightxx.classList.remove('rightxx');
      ul.prepend(rightxx);
      leftxx.previousElementSibling.classList.add('leftxx');

      goInterval();
  }

  //при потери фокуса с экрана слайдер останавливается и добовляется слушатель на событие фокуса. 
  //при получении фокуса срабатывает функция focusInterval, которая запускает слайдер и удалает слушателя из события фокуса
  window.addEventListener('blur', ()=>{clearInterval(timeInterval);
      timeInterval = 0;
      window.addEventListener('focus', focusInterval);
  });

  function focusInterval(){

      goInterval();
      window.removeEventListener('focus', focusInterval);
  };

  //на каждый слайд вешаем обработчики на события новедения/удалиния мыши для добавления визуальных эффектов
  //при наведении курсора интервал удаляется, при уведении курсора - вешается вновь.
  const slides = document.querySelectorAll('.slide');

  for(let i=0; i<slides.length; i++){
      slides[i].addEventListener('mouseover', showFrontSlide);
      slides[i].addEventListener('mouseout', hideFrontSlide);
  };

  function showFrontSlide(){

      if(this.classList.contains('view')){
      clearInterval(timeInterval);
      timeInterval=0;
      this.querySelector('img').classList.add('increase');
      this.querySelector('.slide-front').classList.add('slide-front-view');
      this.querySelector('.slide-click-icon').classList.add('slide-click-icon-view');
      };
  };
  
  function hideFrontSlide(){

      if(this.classList.contains('view')){
      this.querySelector('img').classList.remove('increase');
      this.querySelector('.slide-front').classList.remove('slide-front-view');
      this.querySelector('.slide-click-icon').classList.remove('slide-click-icon-view');
      goInterval();
      };
  };

  goInterval();//запускаем слайдер при загрузке страницы 
  
})();